body {
  margin: 0em;
    position: relative;
    font-family: var(--fontFamily);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  font-size: var(--fontSize);
    direction: rtl;
    color: var(--themeColorText);
    background: var(--themeColorBG);
    transition: 0.1s;
}
  
div.frameDiv1 {
    background: var(--ThemeGPTPurpleVAlpha);
    font-weight: 600;
    font-size: 0.9em;
    padding: 2em 3em;
    margin: 3em 5em;
    color: white;
}
  
div.frameDiv2 {
    /* background: var(--ThemeGPTOrangeDeep); */
    font-weight: 600;
    font-size: 0.9em;
    padding: 2em 3em;
    /* margin: 3em 5em; */
}
#frame2Card {
  margin: 0.4em 1.2em 1em 1.2em;
  padding: 1em 0.5em;
  /* border-radius: 1px; */
  width: 12em;
  height: 16em;
  direction: rtl;
  text-align: -webkit-center;
}

img#frame2Img {
  height: 5em;
  width: 5em;
  padding: 0.8em;
  /* border: 1px solid rgba(0, 0, 0, 0.2); */
  border-radius: 25px;
  margin-bottom: 2em;
  filter: opacity(0.8);
}

#frame2Card * {
  font-size: 1.1em;
}

div.frameDiv3 {
  background: var(--ThemeGPTOrangeDeepAlpha);
  font-weight: 600;
  font-size: 1em;
    padding: 2em 3em;
    margin: 3em 5em;
    text-align: start;
}
  
div.frameDiv3 p {
  line-height: 1.5em;
  font-size: 1.2em;
}

button#trialButton {
  font-size: 1.1em;
  margin: 1em;
  padding: 0.5em 0.9em;
    border-radius: 20px;
  }
  

  
  .App {
    font-size: 1rem;
  }
  
  div#aboutUs p {
    width: 70vw;
  }
  
  section.caruselDiv {
    background: rgba(6, 124, 227, 0.1);
    background: var(--themeColorAlpha);
  padding: 4em 1em;
  }
  
div.carousel-container li {
  align-content: center;
}

div.carousel-container button {
  /* background-color: transparent; */
  background: transparent;
}

div.carousel-container button:hover::before {
  color: white;
}

div.carousel-container button::before {
  color: black;
}

.custom-dot-list-style button {
  background-color: transparent;
}

.custom-dot-list-style li.react-multi-carousel-dot--active button {
  background-color: black;
}

p.cardText {
  display: contents;
  font-size: 1.5em;
}
  
img#tutorSelfie {
  height: 12em;
    border: 1px solid rgba(255, 255, 255, 0.692);
  margin: 3em;
    float: left;
    border-radius: 50%;
  width: 12em;
}