.relative {
    position: relative;
}

.flex {
    display: flex;
}

.center {
    justify-content: center;
}

.between {
    justify-content: space-between;
}

.grid {
    display: grid;
}

.columns {
    grid-auto-flow: column;
    flex-direction: column;
}

.rows {
    grid-auto-flow: row;
    flex-direction: row;
}

.ltr {
    direction: ltr;
}

.rtl {
    direction: rtl;
}

.round {
    border-radius: var(--themeBorderRadius);
}

.rounder {
    border-radius: 20px;
}

.border {
    border: 1px solid;
}

.small {
    font-size: 0.8em;
}

.medium {
    font-size: 0.92em;
}

.large {
    font-size: 1.15em;
}

.xLarge {
    font-size: 1.3em;
}



.fit {
    width: fit-content;
}

.fill {
    width: fill-available;
    width: -webkit-fill-available;
}

.full {
    width: 100%;
}

.fitH {
    height: fit-content;
}

.pointer {
    cursor: pointer;
}

.alignCenter {
    align-items: center;
    align-content: center;
    align-self: center;
}

.boxShadow {
    box-shadow: var(--boxShadow);
}

.boxShadowHover {
    box-shadow: var(--boxShadow);
}

.boxShadowHover:hover {
    /* box-shadow: 2px 2px 12px 1px rgba(0, 0, 0, .955); */
    box-shadow: 0 0.3em 0.9em rgba(0, 0, 0, 0.599);
}

.translateY:hover {
    transform: translateY(-1em);
}

.dropShadow {
    -webkit-filter: drop-shadow(3px 5px 2px rgba(26, 54, 79, 0.7));
    filter: drop-shadow(3px 5px 2px rgba(26, 54, 79, 0.7));
}

.dropShadowWhite {
    -webkit-filter: drop-shadow(3px 5px 2px rgba(254, 248, 233, 0.4));
        filter: drop-shadow(3px 5px 2px rgba(254, 248, 233, 0.4));
}


a,
a:hover,
a:visited,
a:active {
    color: inherit;
    list-style: none;
    text-decoration: none;
    transition: 0.2s;
    text-align: -webkit-center;
}

button {
    padding: 0.5em 0.6em;
    background-color: var(--themeColor);
    border-radius: 0.2em;
    font-weight: bold;
    border: none;
    transition: 0.3s;
    font-size: 0.8em;
}

button:hover {
    opacity: 0.8;
}

.alpha {
    background-color: var(--themeColorAlpha);
}

.themeConst {
    background-color: var(--constThemeColor);
    color: white;
}

.themeConst2 {
    background-color: var(--ThemeGPTOrangeDeep);
    color: black;
}
.rotate {
    animation: rotate 1s forwards;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}










