@media only screen and (max-width: 1100px) {
    body {
        font-size: 16px;
        /* background: rgba(0, 128, 0, 0.346); */
    }

    #frame2Card {
        height: 13em;
    }

    img#frame2Img {
        width: 4em;
        height: 4em;
    }

    img#tutorSelfie {
        margin: 2em;
        width: 10em;
        height: 10em;
    }
}

@media only screen and (max-width: 900px) {
    body {
        font-size: 14px;
        /* background: rgba(128, 107, 0, 0.346); */
    }

    #frame2Card {
        height: 9.5em;
    }

    div.frameDiv1,
    div.frameDiv3,
    #frame2Card * {
        font-size: 0.8em;
    }


    div.frameDiv3 h1 {
        font-size: 1.5em;
    }

    img#frame2Img {
        width: 3.5em;
        height: 3.5em;
    }

    img#tutorSelfie {
        margin: 2em;
        width: 9em;
        height: 9em;
    }

}

@media only screen and (max-width: 750px) {
    body {
        font-size: 12px;
        /* background: rgba(102, 0, 128, 0.346); */
    }

    div.frameDiv1,
    div.frameDiv3,
    #frame2Card * {
        font-size: 0.7em;
    }


}

@media only screen and (max-width: 680px) {
    body {
        font-size: 10px;
        /* background: rgba(128, 53, 0, 0.346); */
    }

    #frame2Card {
        height: 7.5em;
        min-width: 5em;
        margin: 0.7em;
    }

    div.frameDiv2 {
        display: flex;
        flex-wrap: wrap;
    }

    div.frameDiv1,
    div.frameDiv3,
    #frame2Card * {
        font-size: 0.6em;
    }

    div.frameDiv3 h1 {
        font-size: 1em;
    }

    img#tutorSelfie {
        margin: 1em;
        width: 7em;
        height: 7em;
        border: 1px solid rgba(255, 255, 255, 0.651);
    }
}

@media only screen and (max-width: 450px) {
    body {
        font-size: 8px;
        /* background: rgba(128, 53, 0, 0.346); */
    }

    div.frameDiv2 {
        padding: 0.8em;
    }

    #frame2Card {
        height: 7em;
    }

    div.frameDiv1,
    div.frameDiv3,
    #frame2Card * {
